import { createFlickr } from "flickr-sdk"
const { flickr } = createFlickr("99a236cccb6c4e6ce2dedd7bc8115172");

class FlickrHelper {

    private galleryNameList = [];
    private albumPictureMap = new Map<string, any[]>();
    private user_id = '196021119@N04';

    async getAlbumList() {
        if (this.galleryNameList.length !== 0) {
            return this.galleryNameList;
        }

        const res = await flickr("flickr.photosets.getList", {
            user_id: this.user_id,
        })
        
        function getAlbumCover(photoset: any) {
            var server_id = photoset.server;
            var cover_id = photoset.primary;
            var secret_id = photoset.secret;
            return "https://live.staticflickr.com/" + server_id + "/" + cover_id + "_" + secret_id + "_w.jpg";
        }

        this.galleryNameList = res.photosets.photoset.map(
            (photoset: any) => 
                ({
                    title: photoset.title._content,
                    id: photoset.id,
                    description: photoset.description._content,
                    cover_image: getAlbumCover(photoset)
                })
        );
        return this.galleryNameList;
    }

    async getAlbumPictures(id: string, page:Number=1) {

            async function getAlbumPicturesFromAPI(user_id:string, id:string, page:Number) {
                const res = await flickr("flickr.photosets.getPhotos", {
                    user_id: user_id,
                    photoset_id: id,
                    per_page: "200",
                    page: page.toString(),
                    extras: "url_o, url_m",
                })

                var newPageHigh = res.photoset.photo.map(
                    (photo: any) =>
                    ({
                        src: photo.url_o,
                        width: photo.width_o,
                        height: photo.height_o,
                        title: photo.title,
                        srcSet: [
                            { src: photo.url_o, width: photo.width_o, height: photo.height_o },
                            { src: photo.url_m, width: photo.width_m, height: photo.height_m }
                        ]
                    })
                )
                var newPageLow = res.photoset.photo.map(
                    (photo: any) =>
                    ({
                        src: photo.url_m, 
                        width: photo.width_m, 
                        height: photo.height_m,
                        title: photo.title,
                    })
                )
                return {newPageHigh, newPageLow};
            }
            var pageNumber = Number(page)
            var albumList: any[] | undefined = this.albumPictureMap.get(id);
            if (albumList !== undefined) {
                if (albumList.length >= pageNumber) {
                    return albumList[(pageNumber - 1)]
                }
                var newPage = getAlbumPicturesFromAPI(this.user_id, id, page);
                albumList.push(newPage);
                this.albumPictureMap.set(id, albumList);
                return newPage;
            }
            var newNewPage = getAlbumPicturesFromAPI(this.user_id, id, page);
            this.albumPictureMap.set(id, [newNewPage]);
            return newNewPage;

    }

}

export const flickrHelper = new FlickrHelper()