import React from 'react';
import { useState, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import { flickrHelper } from './helpers/flickrHelper';
import HomePage from './pages/HomePage'
import AlbumPage from './pages/AlbumPage';
import NavBar from './components/NavBar';

function App() {
    const [albums, setAlbums] = useState<any[]>([]);
    useEffect(() => {
        async function getAlbums() {
            setAlbums(await flickrHelper.getAlbumList());
        }
        getAlbums();
    }, [albums]);

    return (
    <>
        <div className="pictures">
            <div className="mainBody">
                    <Routes>
                        <Route path="/" element={<HomePage albumList={albums}/>} />
                        {albums.map((album) => 
                            (<Route key={album.id} path={"/" + album.title} element={<AlbumPage album={album}/>}/>))}
                    </Routes>
            </div>
            <NavBar albumList={albums}/>
        </div>

    </>
    )
}

export default App;