import AlbumCover from "../components/AlbumCover";
import './HomePage.css'

function HomePage(props: any) {
    return (
    <>
        <center><h1> Banana Daemon's Wall of Shame </h1></center>
        <div className="albumGrid">
            {props.albumList.map((album: any) => (
            <div className="albumCover">
                <AlbumCover key={album.title} album={album}/>
            </div>))}
        </div>
    </>
    )
}


export default HomePage;