import { NavLink } from "react-router-dom";
import './AlbumCover.css'
function AlbumCover(props: any) {

    return (
        <NavLink to={"/" + props.album.title}>
            <div className="album_cover_wrapper">
                <img src={props.album.cover_image} alt=""/>
                <div className="album_name_layer">
                    <p className="album_name">{props.album.title}</p>
                </div>
            </div>
        </NavLink>
    )
}

export default AlbumCover